	methods: {
		goClass(){
			uni.navigateTo({
				url:'../shopClass/shopClass'
			})
		},
		applyCard(){
			uni.navigateTo({
				url:'../cardRecharge/cardRecharge'
			})
		},
		scanCode(){
			uni.scanCode({
				success(res) {
					uni.setStorage({
						key:'htmlUrl',
						data:res.result,
						success() {
							uni.navigateTo({
								url:'../registerHtml/registerHtml'
							})
						}
					})
				}
			})
		},
		getLocation(){
			uni.getLocation({
			    type: 'wgs84',
			    success: function (res) {
					console.log(res)
			    }
			});
		},
		getTopSwiper(){
			var that = this
			uni.request({
				url:that.baseUrl + 'home/HomePage',
				method:'GET',
				header:{
					
				},
				data:{
					user_id:that.globalData.userInfo.user_id
				},
				success(resq) {
					that.topSwiper =  resq.data.data.swiper
				}
			})
		},
		getImgUrl(){
			var that = this
			uni.request({
				url: this.baseUrl + 'home/getGlobal',
				method:"GET",
				data:{
					user_id: that.globalData.userInfo.user_id
				},
				success(res) {
					that.globalData.imgUrl=res.data.data.img
					that.getTopSwiper()
				}
			})
		}
	},
	created() {
		var that = this
		uni.getStorage({
			key:'userInfo',
			success(res) {
				that.globalData.userInfo = res.data
			}
		}),
		this.getImgUrl()
		this.getLocation()
	}
}
